const PACKAGE_NAME = '@wix/auto_sdk_forms_interactive-form-sessions';

/**
 * Creates an interactive form session for AI-powered conversational form completion.
 *
 * For implementations that require real-time streaming, call [Create Interactive Form Session Streamed](https://dev.wix.com/docs/api-reference/crm/forms/interactive-form-sessions/create-interactive-form-session-streamed) instead.
 */
export function createInteractiveFormSession(
  payload: object
): RequestOptionsFactory<any> {
  function __createInteractiveFormSession({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.forms.ai.v1.interactive_form_session',
      method: 'POST' as any,
      methodFqn:
        'wix.forms.ai.v1.InteractiveFormSessionsService.CreateInteractiveFormSession',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixFormsAiV1InteractiveFormSessionsServiceUrl({
        protoPath: '/v1/interactive-form-sessions',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTFloatToSDKFloat,
            paths: [
              { path: 'responseChunks.numberInputDetails.multipleOf' },
              {
                path: 'responseChunks.numberInputDetails.rangeLimit.maxInclusiveValue',
              },
              {
                path: 'responseChunks.numberInputDetails.rangeLimit.maxExclusiveValue',
              },
              {
                path: 'responseChunks.numberInputDetails.rangeLimit.minInclusiveValue',
              },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __createInteractiveFormSession;
}

